import React, { useEffect, useRef } from 'react'
import frame from '../assets/images/frame.png'

function PresentMySelf({ PlayPresentationVideo }) {
  const presentationVideo = useRef()
  useEffect(() => {
    const reload = () => {
      presentationVideo.current.pause()  
      presentationVideo.current.currentTime = 0  
    }

    // PlayPresentationVideo === true ? presentationVideo.current.play() : reload()
    !PlayPresentationVideo && reload()
  }, [PlayPresentationVideo])
  
  return (
    <div className="page present_my_self">
      <h1 className='present_my_self_title'>Self Introduction</h1>
      
      <img className='video_frame' src={frame} alt='frame png' />
      <div className='present_my_self_video_container'>
        <video ref={presentationVideo} src='https://res.cloudinary.com/multer-cloudinary-tuto/video/upload/v1675450281/introduce_my_self_aj3aey.mp4' controls className='present_my_self_video'>
          <source src='https://res.cloudinary.com/multer-cloudinary-tuto/video/upload/v1675450281/introduce_my_self_aj3aey.mp4' type="video/mp4" />
        </video>
      </div>
    </div>
  )
}

export default PresentMySelf