import React from 'react'
import frame from '../assets/images/frame.png'
import arrow from '../assets/images/arrow.svg'
import { useState } from 'react'
import gsap from 'gsap'
import loudbeats from '../assets/videos/loudbeats.mp4'
import alienpizza from '../assets/videos/alienpizza.mp4'
import bowwoow from '../assets/videos/bowwoow.mp4'


function Projects() {
  const [ProjectCounter, setProjectCounter] = useState(0)
  const projectsData = [
    {
      // video: loudbeats,      
      video: 'https://res.cloudinary.com/multer-cloudinary-tuto/video/upload/v1675478975/loudbeats_yrqcju.mp4',
      title: 'LoudBeats',
      description: "This website offers a wide selection of high-quality headphones, headsets, and AirPods for purchase. The user interface is visually stunning and creatively designed, making it easy to browse and find the perfect pair of headphones for your needs. Whether you're looking for a new pair of AirPods for on-the-go listening or a professional-grade headset for work, this website has something for everyone. With a sleek, modern design and a user-friendly layout, it's a great destination for anyone looking for top-of-the-line headphones and headsets.",
      url: 'https://loudbeats.cyclic.app'
    }
    ,
    {
      // video: alienpizza,
      video: 'https://res.cloudinary.com/multer-cloudinary-tuto/video/upload/v1675478451/alienpizza_p7mwgu.mp4',
      title: 'Alien Pizza',
      description: "In this website you can find the most delicious and mouth-watering pizza you've ever tasted. Its user-friendly interface makes it easy to navigate through the menu and find the perfect pizza for you. With a beautiful and sleek design, this website is not only easy to use, but also visually pleasing to the eye. From classic Margherita to gourmet toppings, it has something for everyone's taste. You can also customize your own pizza by choosing your favorite crust, sauce, and toppings.",
      url: 'https://alienpizza.netlify.app'
    }
    ,
    {
      // video: bowwoow,      
      video: 'https://res.cloudinary.com/multer-cloudinary-tuto/video/upload/v1675478632/bowwoow_ycuihw.mp4',
      title: 'BowWoow.',
      description: "This website offers a wide variety of stylish and high-quality footwear for men, women, and children. It has a visually stunning and user-friendly interface, making it easy to find the perfect pair of shoes. It's developed with the MERN stack, providing a professional-grade technology for web development. With a sleek, modern design, it's a great destination for anyone looking for top-of-the-line shoes. It combines style, functionality, and user experience.",
      url: 'https://bowwoow.cyclic.app'
    }
  ]

  const prevProject = () => {
    ProjectCounter === 0 ? setProjectCounter(0) : setProjectCounter(ProjectCounter-1)

    let timeline = gsap.timeline()
    timeline
    .fromTo('.video_container', { opacity: 0 }, { opacity: 1, duration: .7 })
    .fromTo('.column_2 h1', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: .7 }, '<.3')
    .fromTo('.column_2 p', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: .7 }, '<.2')
    .fromTo('.explore_btn', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: .7 }, '<.2')    
  }

  const nextProject = () => {
    ProjectCounter === projectsData.length-1 ? setProjectCounter(projectsData.length-1) : setProjectCounter(ProjectCounter+1)

    let timeline = gsap.timeline()
    timeline
    .fromTo('.video_container', { opacity: 0 }, { opacity: 1, duration: .7 })
    .fromTo('.column_2 h1', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: .7 }, '<.3')
    .fromTo('.column_2 p', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: .7 }, '<.2')
    .fromTo('.explore_btn', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: .7 }, '<.2')
  }

  return (
    <div className="page projects">
      <h1 className='projects_title'>Projects</h1>
      <div className='projects_container'>
        {
          projectsData.map((p, i) => {
            return (
              <div key={i} className="single_project_container" style={{ opacity: `${i === ProjectCounter ? 1 : 0 }` }}>
                <div className="video_container">
                  <img src={frame} alt="frame png" />
                  <video src={p.video} muted autoPlay loop alt="frame png">
                    <source src={p.video} type="video/mp4" />
                  </video>
                </div>

                <div className='column_2'>
                  <h1>{p.title}</h1>
                  <p>{p.description}</p>
                  <a href={p.url} target='_blank' className='explore_btn' >Explore</a>
                </div>
              </div>
            )
          })
        }

        {
          ProjectCounter !== 0 &&
          <button className='btn prevProj' onClick={prevProject}>
            <img src={arrow} alt="aesthetic arrow svg" />
          </button>
        }
        {      
          ProjectCounter !== projectsData.length-1 &&
          <button className='btn nextProj' onClick={nextProject}>
            <img src={arrow} alt="aesthetic arrow svg" />
          </button>
        }
      </div>
    </div>
  )
}

export default Projects