import React, { useEffect, useRef } from 'react'
import DesktopFirstVideo from '../assets/videos/DesktopFirstVideo.mp4'
import DesktopSecondVideoReversed from '../assets/videos/DesktopSecondVideoReversed.mp4'
import mobileFirstVideo from '../assets/videos/mobileFirstVideo.mp4'
import mobileSecondVideoReversed from '../assets/videos/mobileSecondVideoReversed.mp4'
import gsap from 'gsap';
import { useState } from 'react';

function Landing({ ReverseVideo, PlayFirstVideo }) {
    // let socialMediaAnimation = useRef()
    let video = useRef()
    const [DeviceWidth, setDeviceWidth] = useState('desktop')

    useEffect(() => {
        let timeline = gsap.timeline()
        timeline
        .fromTo('.hi_my_name', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<1.7')
        .fromTo('.my_name', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<.2')
        .fromTo('.border', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<0')
        
        .fromTo('.media', { opacity: 0 }, { opacity: 1, stagger: .3, duration: 1 }, '<.3')
    }, [])
    
    
    useEffect(() => {
        if(PlayFirstVideo) video.current.play()
    }, [PlayFirstVideo])
    
    const checkDeviceWidth = () => {
        window.innerWidth >= 500 ? setDeviceWidth('desktop') : setDeviceWidth('mobile')
    }

    useEffect(() => {
        checkDeviceWidth()
    }, [])

    window.addEventListener('resize', e => {
        e.target.innerWidth >= 500 ? setDeviceWidth('desktop') : setDeviceWidth('mobile')
    })

    return (
        <div className="page landing">
            {
                DeviceWidth === 'desktop' &&
                <video ref={video} src={ReverseVideo ? DesktopSecondVideoReversed : DesktopFirstVideo} muted autoPlay className='video'>
                    <source src={ReverseVideo ? DesktopSecondVideoReversed : DesktopFirstVideo} type="video/mp4" />
                </video>
            }
            {
                DeviceWidth === 'mobile' &&
                <video ref={video} src={ReverseVideo ? mobileSecondVideoReversed : mobileFirstVideo} muted autoPlay className='video'>
                    <source src={ReverseVideo ? mobileSecondVideoReversed : mobileFirstVideo} type="video/mp4" />
                </video>
            }

            <div className="hi">
                <div className="hi_top">
                    <div className="border"></div>
                    <h2 className='hi_my_name'>Hi, I'm</h2>
                    <div className="border"></div>
                </div>
                <h1 className='my_name'>Adam Hrioui</h1>
            </div>

        </div>
    )
}

export default Landing