import React, { useEffect, useState } from 'react'
import { useRef } from 'react'
import DesktopSecondVideo from '../assets/videos/DesktopSecondVideo.mp4'
import mobilesecondVideo from '../assets/videos/mobilesecondVideo.mp4'

function Me({ PlaySecodVideo }) {
  let video = useRef()
  const [DeviceWidth, setDeviceWidth] = useState('desktop')

  const playit = () => {
    console.log(video.current)
    video.current.play()
  }
  
  const checkDeviceWidth = () => {
    window.innerWidth >= 500 ? setDeviceWidth('desktop') : setDeviceWidth('mobile')
  }

  useEffect(() => {
    checkDeviceWidth()
  }, [])

  window.addEventListener('resize', e => {
    e.target.innerWidth >= 500 ? setDeviceWidth('desktop') : setDeviceWidth('mobile')
  })

  useEffect(() => {
    PlaySecodVideo && video.current.play()
  }, [PlaySecodVideo])

  return (
    <div className="page me">
      {
        DeviceWidth === 'desktop' &&
        <video ref={video} src={DesktopSecondVideo} muted autoPlay className='video'>
          <source src={DesktopSecondVideo} type="video/mp4" />
        </video>
      }
      {
        DeviceWidth === 'mobile' &&
        <video ref={video} src={mobilesecondVideo} muted autoPlay className='video'>
          <source src={mobilesecondVideo} type="video/mp4" />
        </video>
      }

      <div className="column">
        <h1 className='me_title'>Who am I?</h1>
        {/* <p onClick={playit}>Adam is a 21-year-old frontend developer with a passion for creating visually appealing and user-friendly websites. He is highly skilled in HTML, CSS, and JavaScript and has a keen eye for design. Adam is always eager to learn and stay up-to-date with the latest web development trends and technologies. He is a team player and enjoys collaborating with other developers to bring creative ideas to life. With his talent and drive, Adam is sure to make a positive impact in the world of web development.</p> */}
        {/* <p>Welcome to my portfolio! My name is Adam and I am a 21-year-old frontend developer. I am a hard worker who is dedicated to creating beautiful and functional websites for my clients. With my skills and experience, I am able to create visually appealing and user-friendly websites that are optimized for both mobile and desktop devices. I am passionate about my work and am always striving to improve my skills and knowledge. As a freelancer, I am able to work with a variety of clients from different industries, which allows me to bring a fresh perspective to each project. I am confident that I can create a website that will exceed your expectations and help your business succeed. Take a look at my portfolio to see some of my past projects and feel free to contact me if you have any questions or would like to discuss a potential project.</p> */}
        <p>Welcome to my portfolio! My name is Adam, a 21-year-old frontend developer. I am dedicated to creating beautiful and functional websites for my clients. My skills and experience allow me to create visually appealing and user-friendly websites that are optimized for both mobile and desktop devices. As a freelancer, I am able to work with a variety of clients from different industries. I am confident that I can create a website that will exceed your expectations and help your business succeed. Take a look at my portfolio and feel free to contact me if you have any questions or would like to discuss a potential project.</p>
      </div>
    </div>
  )
}

export default Me