import React, { useState } from 'react'
import adam from '../assets/images/adam.png'
import axios from 'axios'

function Contact() {
  const [Name, setName] = useState('')
  const [Email, setEmail] = useState('')
  const [LookingFor, setLookingFor] = useState('')
  const [Message, setMessage] = useState('')
  const [Send, setSend] = useState('Send')

  const sendMessage = async e => {
    setSend('Sendind...')
    e.preventDefault()
    const res = await axios.post('/api/contact', {
      name: Name, email: Email, lookingFor: LookingFor, message: Message
    })
    if(res) {
      setName('')
      setEmail('')
      setLookingFor('')
      setMessage('')
      setSend('Message sent')
    }
  }
  
  return (
    <div className="page Contact">
      <h1 className='contact_title'>Contact</h1>
      
      <div className='contact_container'>
        <div className='my_photo'>
          <img src={adam} alt="" />
        </div>

        <form className='form'>
          <input type="text" placeholder='Name...' autoComplete='none'  onChange={e => setName(e.target.value)} value={Name} />
          <input type="email" placeholder='Email...' autoComplete='none'  onChange={e => setEmail(e.target.value)} value={Email} />
          <input type="text" placeholder="I'm looking for..." autoComplete='none'  onChange={e => setLookingFor(e.target.value)} value={LookingFor} />
          <textarea cols="30" rows="5" placeholder='Message...' autoComplete='none' onChange={e => setMessage(e.target.value)} value={Message}></textarea>
          <button className='send_btn' onClick={sendMessage}>{Send}</button>
        </form>
      </div>
    </div>
  )
}

export default Contact