import React, { useEffect, useRef } from 'react'
import Landing from './Landing'
import arrow from '../assets/images/arrow.svg'
import vector from "../assets/images/vector.svg";
import logo from "../assets/images/logo.png";
import gsap from 'gsap'
import Me from './Me'
import Skills from './Skills'
import { useState } from 'react';
import Projects from './Projects';
import PresentMySelf from './PresentMySelf';
import Contact from './Contact';

function Home() {
  const [Counter, setCounter] = useState(2)
  const [ReverseVideo, setReverseVideo] = useState(false)
  const [PlaySecodVideo, setPlaySecodVideo] = useState(false)
  const [PlayFirstVideo, setPlayFirstVideo] = useState(false)
  const [PlayPresentationVideo, setPlayPresentationVideo] = useState(false)
  const [DisableBtn, setDisableBtn] = useState(false)
  let vactorAnimation = useRef()
  let logoAnimation = useRef()
  let increaseBtn = useRef()
  let decreaseBtn = useRef()

  let dots = [0, 0, 0, 0, 0, 0]

  useEffect(() => {
    window.addEventListener('load', () => {
      setTimeout(() => {
        window.scrollTo(0, 10)
      }, 10)
    })

    let timeline = gsap.timeline()
    timeline
    .fromTo(vactorAnimation.current, { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1.5 }, '<1.7')
    .fromTo(logoAnimation.current, { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1.5 }, '<0')
    .fromTo('.dot', { scale: 0 }, { scale: 1, stagger: .2, duration: .6 }, '<2')
    .fromTo('.btn', { scale: 0 }, { scale: 1, stagger: .3, duration: .7 }, '<.4')
    .fromTo('.social_media', { zIndex: 10 }, { zIndex: 1080, duration: 1 }, '<.3')    
  }, [])

  const decrease = () => {
    setTimeout(() => {
      window.scrollTo(0, 10)
    }, 0)

    setReverseVideo(false)
    setPlayFirstVideo(false)
    Counter === 2 ? setCounter(2) : setCounter(Counter-1)
    setDisableBtn(true)
    setTimeout(() => {
      setDisableBtn(false)
    }, 4000);

    if(Counter === 8) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.Contact', { opacity: 0, pointerEvents: 'none' }, { opacity: 1, pointerEvents: 'all', duration: .4 })
      .fromTo('.contact_title', { opacity: 0 }, { opacity: 1, duration: .4 }, '<.2')
      .fromTo('.contact_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 })
      
      .fromTo('.components', { opacity: 1 }, { opacity: 0, duration: 1 })
      .fromTo('.my_photo', { opacity: 0 }, { opacity: 1, duration: .7 }, '<.4')
      .fromTo('.form *', { opacity: 0, y: 30 }, { opacity: 1, y: 0, stagger: .3, duration: .4 })
    }

    if(Counter === 7) {
      let timeline = gsap.timeline()
      timeline
      
      .fromTo('.send_btn', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.form *', { opacity: 1 }, { opacity: 0, duration: .4 }, '<.2')
      .fromTo('.my_photo', { opacity: 1 }, { opacity: 0, duration: .7 }, '<.3')
      .fromTo('.components', { opacity: 0 }, { opacity: 1, duration: 1 }, '<.3')
      .fromTo('.contact_title', { y: '-35vh', fontSize: '35px' }, { y: 0, fontSize: '70px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.contact_title', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.Contact', { opacity: 1, pointerEvents: 'all' }, { opacity: 0, pointerEvents: 'none', duration: .4 })

      
      .fromTo('.present_my_self', { opacity: 0, zIndex: 1 }, { opacity: 1, zIndex: 1080, duration: .4 })      
      .fromTo('.present_my_self_title', { opacity: 0 }, { opacity: 1, duration: .4 }, '<.2')
      .fromTo('.present_my_self_title', { y: 0, fontSize: '70px', lineHeight: '70px' }, { y: '-35vh', fontSize: '35px', lineHeight: '33px', ease: 'Power4.easeInOut', duration: 1 })
      
      .fromTo('.present_my_self_video_container', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.video_frame', { opacity: 0 }, { opacity: 1, duration: .4 }, '<0')

      setTimeout(() => {
        setPlayPresentationVideo(true)
      }, 1500)
    }

    if(Counter === 6) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.video_frame', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.present_my_self_video_container', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.present_my_self_title', { y: '-35vh', fontSize: '35px', lineHeight: '33px' }, { y: 0, fontSize: '70px', lineHeight: '70px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.present_my_self_title', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.present_my_self', { opacity: 1, zIndex: 1080 }, { opacity: 0, zIndex: 1, duration: .4 })

      .fromTo('.page.skills', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.skills_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.column_3', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.column_3 h1', { opacity: 0 }, { opacity: 1, stagger: .3, duration: .4 })
      .fromTo('.column_3 ul li', { opacity: 0, x: -50 }, { opacity: 1, x: 0, stagger: .1, duration: .2 })
    }

    if(Counter === 5) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.column_3', { opacity: 1 }, { opacity: 0, stagger: .3, duration: .4 })
      .fromTo('.skills_title', { y: '-35vh', fontSize: '35px' }, { y: 0, fontSize: '70px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.page.skills', { opacity: 1 }, { opacity: 0, duration: .4 })
      
      .fromTo('.page.projects', { opacity: 0, zIndex: 10 }, { opacity: 1, zIndex: 1090, duration: .4 })
      .fromTo('.projects_title', { opacity: 0 }, { opacity: 1, duration: .4 }, '<.2')
    
      .fromTo('.components', { opacity: 1, zIndex: 1090 }, { opacity: 0, zIndex: 10, duration: 1 })
      // .fromTo('.projects_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 }, '<.6')
      .fromTo('.projects_title', { opacity: 1 }, { opacity: .2, duration: .4 })

      .fromTo('.video_container', { opacity: 0 }, { opacity: 1, duration: 1 })
      .fromTo('.column_2 h1', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.easeOut', duration: 1 }, '<.3')
      .fromTo('.column_2 p', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.easeOut', duration: 1 }, '<.3')
      .fromTo('.explore_btn', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.easeOut', duration: 1 }, '<.3')
      .fromTo('.prevProj', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<.1')
      .fromTo('.nextProj', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<0')
    }
    
    if(Counter === 4) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.video_container', { opacity: 1 }, { opacity: 0, duration: .7 }, '<0')
      .fromTo('.column_2 h1', { opacity: 1 }, { opacity: 0, duration: .7 }, '<.1')
      .fromTo('.column_2 p', { opacity: 1 }, { opacity: 0, duration: .7 }, '<.2')
      .fromTo('.explore_btn', { opacity: 1 }, { opacity: 0, duration: .7 }, '<.2')
      
      .fromTo('.prevProj', { opacity: 1, scale: 1 }, { opacity: 0, scale: 0, duration: .7 }, '<0')
      .fromTo('.nextProj', { opacity: 1, scale: 1 }, { opacity: 0, scale: 0, duration: .7 }, '<0')
      .fromTo('.components', { opacity: 0, zIndex: 10 }, { opacity: 1, zIndex: 1090, duration: 1 }, '<.4')
      // .fromTo('.projects_title', { y: '-35vh', fontSize: '35px' }, { y: 0, fontSize: '70px', ease: 'Power4.easeInOut', duration: 1 }, '<.4')

      .fromTo('.projects_title', { opacity: .2 }, { opacity: 1, duration: .4 })
      .fromTo('.page.projects', { opacity: 1, zIndex: 1090 }, { opacity: 0, zIndex: 10, duration: .4 })

      .fromTo('.page.me', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.column h1', { opacity: 0, y: -50 }, { opacity: 1, y: 0, duration: 1 }, '<.4')
      .fromTo('.column p', { opacity: 0, y: -50 }, { opacity: 1, y: 0, duration: 1 }, '<.3')
    }
    if(Counter === 3) {
      setTimeout(() => {
        setReverseVideo(true)
        setPlayFirstVideo(true)
      }, 600)

      let timeline = gsap.timeline()
      timeline
      
      .fromTo('.column h1', { opacity: 1, y: 0 }, { opacity: 0, y: 50, duration: .5 })
      .fromTo('.column p', { opacity: 1, y: 0 }, { opacity: 0, y: 50, duration: .5 }, '<.2')
      .fromTo('.page.me', { opacity: 1 }, { opacity: 0, duration: .4 })
      
      .fromTo('.page.landing', { opacity: 0, zIndex: 1 }, { opacity: 1, zIndex: 1080, duration: .1}, '<0')
      .fromTo('.hi_my_name', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<1.7')
      .fromTo('.my_name', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<.2')
      .fromTo('.border', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<0')
      .fromTo('.social_media', { zIndex: 10 }, { zIndex: 1080, duration: 1 }, '<.0')
      .fromTo('.media', { opacity: 0 }, { opacity: 1, stagger: .3, duration: 1 }, '<.3')
    }
  }

  const increase = () => {
    setTimeout(() => {
      window.scrollTo(0, 10)
    }, 0)

    setPlaySecodVideo(false)
    Counter === 8 ? setCounter(8) : setCounter(Counter+1)
    setDisableBtn(true)
    setTimeout(() => {
      setDisableBtn(false)
    }, 4000);

    if(Counter === 1) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.hi_my_name', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<1.7')
      .fromTo('.my_name', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<.2')
      .fromTo('.border', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<0')
      .fromTo('.social_media', { zIndex: 10 }, { zIndex: 1080, duration: 1 }, '<.0')
      .fromTo('.media', { opacity: 0 }, { opacity: 1, stagger: .3, duration: 1 }, '<.3')
    }
    if(Counter === 2) {
      console.log("Counter: ", Counter)
      setTimeout(() => {
        setPlaySecodVideo(true)
      }, 1300);
      let timeline = gsap.timeline()
      timeline
      .fromTo('.hi_my_name', { opacity: 1 }, { opacity: 0, duration: .4})
      .fromTo('.my_name', { opacity: 1 }, { opacity: 0, duration: .4}, '<0')
      .fromTo('.border', { opacity: 1 }, { opacity: 0, duration: .4}, '<0')
      .fromTo('.social_media', { zIndex: 1080 }, { zIndex: 10, duration: 1 }, '<0')
      .fromTo('.media', { opacity: 1 }, { opacity: 0, duration: .4}, '<0')
      .fromTo('.page.landing', { opacity: 1 }, { opacity: 0, duration: .4})
      
      .fromTo('.page.me', { opacity: 0 }, { opacity: 1, duration: .1 })
      .fromTo('.page.me .column h1', { opacity: 0, y: -50 }, { opacity: 1, y: 0, duration: 1 }, '<1.4')
      .fromTo('.page.me .column p', { opacity: 0, y: -50 }, { opacity: 1, y: 0, duration: 1 }, '<.3')
      
    }

    if(Counter === 3) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.page.me .column h1', { opacity: 1, y: 0 }, { opacity: 0, y: 50, duration: .5 })
      .fromTo('.page.me .column p', { opacity: 1, y: 0 }, { opacity: 0, y: 50, duration: .5 }, '<.2')
      .fromTo('.page.me', { opacity: 1 }, { opacity: 0, duration: .4 })
      
      .fromTo('.page.projects', { opacity: 0, zIndex: 10 }, { opacity: 1, zIndex: 1090, duration: .4 })
      .fromTo('.projects_title', { opacity: 0 }, { opacity: 1, duration: .4 }, '<.2')
    
      .fromTo('.components', { opacity: 1, zIndex: 1090 }, { opacity: 0, zIndex: 10, duration: 1 })
      // before 1 .fromTo('.projects_title', { y: 0, fontSize: '70px', x: '0%' }, { y: '-35vh', fontSize: '35px', x: '0%', ease: 'Power4.easeInOut', duration: 1 })
      // .fromTo('.projects_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.projects_title', { opacity: 1 }, { opacity: .2, duration: .4 })

      // before 0 .fromTo('.projects_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 }, '<.6')
      .fromTo('.video_container', { opacity: 0 }, { opacity: 1, duration: 1 })
      .fromTo('.column_2 h1', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.easeOut', duration: 1 }, '<.3')
      .fromTo('.column_2 p', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.easeOut', duration: 1 }, '<.3')
      .fromTo('.explore_btn', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.easeOut', duration: 1 }, '<.3')
      .fromTo('.prevProj', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<.1')
      .fromTo('.nextProj', { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }, '<0')
    }
    
    if(Counter === 4) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.nextProj', { opacity: 1, scale: 1 }, { opacity: 0, scale: 0, duration: 1 })
      .fromTo('.prevProj', { opacity: 1, scale: 1 }, { opacity: 0, scale: 0, duration: 1 }, '<0')
      .fromTo('.explore_btn', { opacity: 1, y: 0 }, { opacity: 0, y: -50, ease: 'Power4.easeOut', duration: 1 }, '<.1')
      .fromTo('.column_2 p', { opacity: 1, y: 0 }, { opacity: 0, y: -50, ease: 'Power4.easeOut', duration: 1 }, '<.1')
      .fromTo('.column_2 h1', { opacity: 1, y: 0 }, { opacity: 0, y: -50, ease: 'Power4.easeOut', duration: 1 }, '<.1')
      .fromTo('.video_container', { opacity: 1 }, { opacity: 0, duration: 1 }, '<.1')
      // .fromTo('.projects_title', { y: '-35vh', fontSize: '35px' }, { y: 0, fontSize: '70px', ease: 'Power4.easeInOut', duration: 1 }, '<.5')
      .fromTo('.projects_title', { opacity: .2 }, { opacity: 1, duration: .4 })

      .fromTo('.components', { opacity: 0, zIndex: 10 }, { opacity: 1, zIndex: 1090, duration: 1 })
      // .fromTo('.projects_title', { opacity: 1 }, { opacity: 0, duration: .4 }, '<.2')
      .fromTo('.page.projects', { opacity: 1, zIndex: 1090 }, { opacity: 0, zIndex: 10, duration: .4 })
      
      .fromTo('.page.skills', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.skills_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.column_3', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.column_3 h1', { opacity: 0 }, { opacity: 1, stagger: .3, duration: .4 })
      .fromTo('.column_3 ul li', { opacity: 0, x: -50 }, { opacity: 1, x: 0, stagger: .1, duration: .2 })
    }

    if(Counter === 5) {
      let timeline = gsap.timeline()
      timeline
      .fromTo('.column_3', { opacity: 1 }, { opacity: 0, stagger: .3, duration: .4 })
      .fromTo('.skills_title', { y: '-35vh', fontSize: '35px' }, { y: 0, fontSize: '70px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.page.skills', { opacity: 1 }, { opacity: 0, duration: .4 })
      
      .fromTo('.present_my_self', { opacity: 0, zIndex: 1 }, { opacity: 1, zIndex: 1080, duration: .4 })      
      .fromTo('.present_my_self_title', { opacity: 0 }, { opacity: 1, duration: .4 }, '<.2')
      .fromTo('.present_my_self_title', { y: 0, fontSize: '70px', lineHeight: '70px' }, { y: '-35vh', fontSize: '35px', lineHeight: '33px', ease: 'Power4.easeInOut', duration: 1 })
      
      .fromTo('.present_my_self_video_container', { opacity: 0 }, { opacity: 1, duration: .4 })
      .fromTo('.video_frame', { opacity: 0 }, { opacity: 1, duration: .4 }, '<0')

      setTimeout(() => {
        setPlayPresentationVideo(true)
      }, 1500)
    }
    if(Counter === 6) {
      setPlayPresentationVideo(false)

      let timeline = gsap.timeline()
      timeline
      .fromTo('.video_frame', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.present_my_self_video_container', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.present_my_self_title', { y: '-35vh', fontSize: '35px', lineHeight: '33px' }, { y: 0, fontSize: '70px', lineHeight: '70px', ease: 'Power4.easeInOut', duration: 1 })
      .fromTo('.present_my_self_title', { opacity: 1 }, { opacity: 0, duration: .4 })
      .fromTo('.present_my_self', { opacity: 1, zIndex: 1080 }, { opacity: 0, zIndex: 1, duration: .4 })

      .fromTo('.Contact', { opacity: 0, pointerEvents: 'none' }, { opacity: 1, pointerEvents: 'all', duration: .4 })
      .fromTo('.contact_title', { opacity: 0 }, { opacity: 1, duration: .4 }, '<.2')
      .fromTo('.contact_title', { y: 0, fontSize: '70px' }, { y: '-35vh', fontSize: '35px', ease: 'Power4.easeInOut', duration: 1 })
      
      .fromTo('.components', { opacity: 1 }, { opacity: 0, duration: 1 })
      .fromTo('.my_photo', { opacity: 0 }, { opacity: 1, duration: .7 }, '<.4')
      .fromTo('.form *', { opacity: 0, y: 30 }, { opacity: 1, y: 0, stagger: .3, duration: .4 })
      
    }
  }
  
  return (
    <div className='home'>
      <img src={vector} ref={vactorAnimation} alt="Adam Hrioui" className='vector' />
      <img src={logo} ref={logoAnimation} alt="Adam Hrioui" className='logo' />
      
      <Landing ReverseVideo={ReverseVideo} PlayFirstVideo={PlayFirstVideo} />
      <Me PlaySecodVideo={PlaySecodVideo} />
      <Projects />
      <Skills />
      <PresentMySelf PlayPresentationVideo={PlayPresentationVideo} />
      <Contact />

      <div className="components">
        {
          dots.map((d, i) => {
            return (
              <span key={i} className="dot" style={{ transform: `scale(${i === Counter - 2 ? 1.5 : 1})` }}></span>
            )
          })
        }
      </div>

      <div className="btns">
        {
          Counter !== 2 &&
          <button className='btn prev' onClick={decrease} ref={decreaseBtn} disabled={DisableBtn} >
            <img src={arrow} alt="aesthetic arrow svg" />
          </button>
        }
        {
          Counter !== 7 &&
          <button className='btn next' onClick={increase} ref={increaseBtn} disabled={DisableBtn} >
            <img src={arrow} alt="aesthetic arrow svg" />
          </button>
        }
      </div>


      <div className="social_media">
        <a href="https://ma.linkedin.com/in/adam-hrioui-23679b231" target='_blank'>
            <span className='media'>IN</span>
        </a>
        <a href="https://twitter.com/AdamHrioui2" target='_blank'>
            <span className='media'>TW</span>
        </a>
        <a href="https://github.com/AdamHrioui1" target='_blank'>
            <span className='media'>GIT</span>
        </a>
      </div>
      {/* <div className="home_border"></div> */}
    </div>
  )
}

export default Home