import gsap from 'gsap'
import React from 'react'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import logo from '../assets/images/logo.png'
import CountUp from 'react-countup';
import video1 from '../assets/videos/DesktopFirstVideo.mp4'
import video2 from '../assets/videos/DesktopSecondVideo.mp4'
import video3 from '../assets/videos/mobileFirstVideo.mp4'
import video4 from '../assets/videos/mobilesecondVideo.mp4'

function PreLanding() {
    useEffect(() => {
        let timeline = gsap.timeline()
        timeline
        .fromTo('.loading_bar', { width: 0 }, { width: '100%', duration: 2, ease: 'Power4.InOut' })
        .fromTo('.loading_container', { opacity: 1 }, { opacity: 0, duration: .1 }, '<3')
        .fromTo('.preLanding_logo', { scale: .5, opacity: 0 }, { scale: 1, opacity: 1, ease: 'Power4.inOut', duration: 2 }, '<.7')
        .fromTo('.preLanding_btn', { opacity: 0, y: 50 }, { opacity: 1, y: 0, ease: 'Power4.InOut', duration: 2 }, '<.7')
    }, [])
    
    return (
        <div className="page preLanding">
            <div className="loading_container">
                <h1 className='loading_number'>
                    <CountUp start={0} end={100} duration={2} />%
                </h1>
                <div className="loading_bar_container">
                    <div className="loading_bar"></div>
                </div>
            </div>
            <img className='preLanding_logo' width={250} src={logo} alt="adam hrioui" />
            <Link to='/portfolio' className='preLanding_btn'>Explore</Link>

            <video style={{ position: 'absolute', top: '100%', left: '100%', opacity: 0, userSelect: 'none' }} src={video1} controls className='present_my_self_video' autoPlay muted >
                <source src={video1} type="video/mp4" />
            </video>
            <video style={{ position: 'absolute', top: '100%', left: '100%', opacity: 0, userSelect: 'none' }} src={video2} controls className='present_my_self_video' autoPlay muted >
                <source src={video2} type="video/mp4" />
            </video>
            <video style={{ position: 'absolute', top: '100%', left: '100%', opacity: 0, userSelect: 'none' }} src={video3} controls className='present_my_self_video' autoPlay muted >
                <source src={video3} type="video/mp4" />
            </video>
            <video style={{ position: 'absolute', top: '100%', left: '100%', opacity: 0, userSelect: 'none' }} src={video4} controls className='present_my_self_video' autoPlay muted >
                <source src={video4} type="video/mp4" />
            </video>
        </div>
    )
}

export default PreLanding